import AuthForm from "Components/AuthForm";

const SuccessRestorePage = ({
	delegations : {
		handleClick,
	},
}) => {
	return (
		<div className="SuccessRestorePage">
			<AuthForm
				title="¡Contraseña Restablecida!"
				subTitle="¡Tu contraseña ha sido restablecida exitosamente!"
				labelBtn="Iniciar Sesión"
				handleSumbitForm={handleClick}
			/>
			{/* </AuthForm> */}
		</div>
	);
};

export default SuccessRestorePage;
