import { useHistory } from "react-router-dom";

//Import Own Components
import SuccessRestorePage from "./SuccessRestorePage";

const SuccessRestorePageContainer = () => {
	const history = useHistory();

	const handleClick = () => history.replace("/auth/login");

	return (
		<SuccessRestorePage
			delegations={{
				handleClick,
			}}
		/>
	);
};

export default SuccessRestorePageContainer;
